* {
  box-sizing: border-box;
}

:root {
  --font-family-montserrat: "Montserrat", sans-serif;
  --font-family-dela-gothic: "Dela Gothic One", sans-serif;
  --font-weight-light: 500;
  --font-weight-normal: 600;
  --font-weight-bold: 700;
  --font-size-xs: 12px;
  --font-size-s: 14px;
  --font-size-m: 16px;
  --font-size-l: 18px;
  --primary-grey-light: #f6f5f4;
  --primary-grey-dark: #757575;
  --primary-grey-medium: #e0e0e0;
  --primary-grey-icon: #bababa;
  --primary-black: #000;
  --primary-pink-dark: #945069;
  --primary-pink-light: #f2d4dc;
  --primary-cyan-dark: #2b788b;
  --primary-cyan-light: #c3dce3;
  --secondary-green: #639b6d;
  --secondary-green-faded: #639b6d4d;
  --secondary-yellow: #c4a24c;
  --secondary-yellow-faded: #c4a24c4d;
  --secondary-orange: #cb5b43;
  --secondary-orange-faded: #cb5b434d;
  --secondary-cyan: #5996a5;
}

body {
  font-family: var(--font-family-montserrat);
  color: var(--primary-black);
  font-weight: var(--font-weight-normal);
  background-color: var(--primary-grey-light);
  margin: 0;
}

.link {
  color: var(--primary-grey-dark);
  cursor: pointer;
  text-decoration: none;
}

.link:hover {
  color: var(--primary-black);
}

.icon {
  color: var(--primary-grey-icon);
  cursor: pointer;
}

.icon:hover {
  color: var(--primary-black);
}

.separator_horizontal {
  border: 0;
  border-top: 1px solid var(--primary-grey-medium);
}

.separator_vertical {
  border: 0;
  border-right: 1px solid var(--primary-grey-medium);
}

.button {
  box-shadow: none;
  background: inherit;
  cursor: pointer;
  font-family: var(--font-family-montserrat);
  font-size: var(--font-size-m);
  font-weight: var(--font-weight-bold);
  letter-spacing: -.64px;
  border: none;
  outline: none;
  padding: 0;
  line-height: 100%;
  transition: all .1s;
}

.button:active {
  transform: scale(.92);
}

.button_ghost {
  color: var(--primary-grey-dark);
  background-color: #0000;
}

.button_ghost:hover {
  color: var(--primary-black);
}

.button_success {
  background: var(--secondary-green);
  color: var(--primary-grey-light);
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  display: flex;
}

.button_alert {
  background: var(--secondary-orange-faded);
  color: var(--secondary-orange);
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: 12px 20px;
  display: flex;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

